<template>

    <div id="Circular_Maker">
        <b-table :style="`background:red;`" striped hover :items="IT"></b-table>

        <!-- 화면상단 -->
        <div class="test" :style="`text-align:center; padding-top:60px;`">
            <p :style="`margin:0px;`">년, 월, 주, 일, 시</p>
            <p :style="`display:inline-block; font-size:35px;`">미리 연마</p>
        </div>


        <!-- 메이커 -->
        <div id="maker" :style="`height:${diam}px; margin: 20px; position: relative;`">
            <Mycircle v-for="(item,i) in Circle_Maker" :key="i"
                :diam=diam-(i*50) :Items=item.items :On_i=item.On_i :Degree=item.Degree 
                :FontSize=item.FontSize :grad=item.grad :multiple_num=item.multiple_num />
        </div>


        <!-- 화면하단 -->
        <p v-for="(item,i) in Circle_Maker.slice().reverse()" :key="i"
        :style="`text-align:center;`">{{item.display}} :
            
            <span :style="`text-align:center; color:#ffee10;`"
            >{{item.remainH}}</span>

            <ProgressBar_Linear :Amount=item.remainMS :Total=item.totalMS :style="`margin:0 20px;`"/>
         </p>

    </div>


    

</template>
<script>
import Mycircle from './Mycircle.vue'
import ProgressBar_Linear from './ProgressBar_Linear.vue'

export default {
    mounted() {  
        this.setThisday();
        this.setRemain();

        this.setDiam();
    },  
    
    data(){
        return{
            birth:new Date('1992-1-9 05:00:00'),
            death:new Date('2073-10-14 05:00:00'),
            today:new Date(),
            diam:100,
            years : {FirstDate:Date, LastDate:Date, remainMS: Number, remainH: Number, display:"이번 생 남은 일",name:"년", items: [],Degree :Number,FontSize:7},
            // months : {items: [],On_i: Number,Degree :Number},
            // months : {items: ["Jan","Feb","Mar","Apr","May","Jun","Jul","Aug","Sep","Oct","Nov","Dec",]
            months :{FirstDate:Date, LastDate:Date,remainMS: Number, remainH: Number,display:"올 해 남은 일 수",name:"월",items: [],Degree :Number,FontSize:13} ,
            // '1월','2월','3월','4월','5월','6월','7월','8월','9월','10월','11월','12월']
            date : {FirstDate:Date, LastDate:Date,remainMS: Number, remainH: Number,display:"이 달 남은 시간",ame:"일",items: [],Degree :Number,FontSize:13},            
            days : {FirstDate:Date, LastDate:Date,remainMS: Number, remainH: Number,display:"이번 주 남은 시간",name:"주",items: ["월","화","수","목","금","토","일"],Degree :Number,FontSize:13} ,
            // days : {items: ["Mon","Tue","Wed","Thur","Fri","Sat","Sun"],Degree :Number,FontSize:13} ,
            hours : {FirstDate:Date, LastDate:Date,remainMS: Number, remainH: Number,display:"오늘 남은 시간", name:"시",items: [],Degree :Number,FontSize:8},          
            
            Circle_Maker : [],
            Display : [],
            IT: [
                { age: 40, first_name: 'Dickerson', last_name: 'Macdonald' },
                { age: 21, first_name: 'Larsen', last_name: 'Shaw' },
                { age: 89, first_name: 'Geneva', last_name: 'Wilson' },
                { age: 38, first_name: 'Jami', last_name: 'Carney' }
                ],
            // day :[],
            // 
            // days : ["월","화","수","목","금","토","일"],
        }
    },
    methods : {
        setDiam()
        {
            if(window.screen.width<window.screen.height)
                this.diam = window.screen.width-50;
            else
                this.diam = window.screen.height-300;
        },
        PushPush(Items, Adder,Unit,i)
        {
            let Date_S = new Date(Items.FirstDate);
            // let Adder = new Date(0,0,0,0,0);
            // for(var i=0;(Date_E-(Date_S+Adder))>0;Adder+=Add;i++)
            console.log(Items+" "+ Adder+" "+Unit+" "+i+" "+Date_S);

            for(;(Items.LastDate-(Date_S))>0;Date_S+=Adder,i++)            
               {
                   console.log(Date_S);
                   Items.push(i+Unit);
               } 
        },
        DateDic(today)
        {
            var yearBegin, MonthBegin, WeekBegin, DayBegin;
            var yearEnd, MonthEnd, WeekEnd, DayEnd;
            // var yearBegin = new Date(),MonthBegin = new Date(),WeekBegin = new Date(),DayBegin = new Date();
            // var yearEnd = new Date(),MonthEnd = new Date(),WeekEnd = new Date(), DayEnd = new Date();
            
            yearBegin = new Date(today.getFullYear(),0,1);
            MonthBegin = new Date(today.getFullYear(),today.getMonth(),1);

            WeekBegin = new Date();
            WeekBegin.setDate(today.getDate() - (((1 + 7 - today.getDay()) % 7) || 7));//다음월요일 00시.
            WeekBegin.setHours(0,0,0);
            DayBegin = new Date(today);
            DayBegin.setHours(0,0,0);

            yearEnd = new Date(today.getFullYear()+1,0,1,0,0,0);
            MonthEnd = new Date(today.getFullYear(),today.getMonth()+1,1);

            WeekEnd = new Date();
            WeekEnd.setDate(this.today.getDate() + (((1 + 7 - this.today.getDay()) % 7) || 7));//다음월요일 00시.
            WeekEnd.setHours(0,0,0);

            DayEnd =  new Date(this.today.getFullYear(), this.today.getMonth(), this.today.getDate()+1)
            DayEnd.setHours(0,0,0);                

            return {
                yearBegin:yearBegin, MonthBegin:MonthBegin, WeekBegin:WeekBegin, DayBegin:DayBegin,
                yearEnd:yearEnd, MonthEnd:MonthEnd,WeekEnd:WeekEnd,DayEnd:DayEnd,
            };
        },
        setThisday()
            {
                var obj = this.DateDic(this.today);
                this.years.FirstDate = this.birth;
                this.years.LastDate = this.death;
                this.months.FirstDate = obj.yearBegin;
                this.months.LastDate = obj.yearEnd;
                this.date.FirstDate = obj.MonthBegin;
                this.date.LastDate = obj.MonthEnd;
                this.days.FirstDate = obj.WeekBegin;
                this.days.LastDate = obj.WeekEnd;
                this.hours.FirstDate = obj.DayBegin;
                this.hours.LastDate = obj.DayEnd;

             // for(var i=1;i<=31;i++)this.date.items.push(i);//+'일');
                //눈금은 있되, 글자는 없게하기. 
                for(var i=this.birth.getFullYear();i<this.death.getFullYear()-2;i+=1)this.years.items.push(i+'년');
                for(i=1;i<=12;i+=1)this.months.items.push(i+'월');
                for(i=1;i<=31;i+=1)this.date.items.push(i+'일');
                for(i=0;i<=23;i+=1)this.hours.items.push(i);
                // for(i=0;i<=12;i++)this.hours.items.push(i+'am');
                // for(i=1;i<=11;i++)this.hours.items.push(i+'pm');

            // if(false)
            //     {this.PushPush(this.years.items, new Date(1,0,0),"년",1);
            //     this.PushPush(this.months.items,new Date(0,1,0),"월",1);
            //     this.PushPush(this.date.items,new Date(0,0,1),"일",1);
            //     this.PushPush(this.days.items,new Date(0,0,1),"요일",1);
            //     this.PushPush(this.hours.items,new Date(0,0,0,1,0),"시",1);
            //     }

                

                

              
                
                this.years.On_i = this.today.getFullYear()- this.birth.getFullYear();//이 아이만. 
                this.months.On_i= this.today.getMonth();
                this.date.On_i= this.today.getDate()-1;
                this.days.On_i= this.today.getDay()-1;
                this.hours.On_i= this.today.getHours();


                this.Circle_Maker.push(this.years);
                this.Circle_Maker.push(this.months);
                this.Circle_Maker.push(this.date);
                this.Circle_Maker.push(this.days);
                this.Circle_Maker.push(this.hours);
                
                // this.Circle_Maker.push(this.hours);
                // this.Circle_Maker.push(this.days);
                // this.Circle_Maker.push(this.date);
                // this.Circle_Maker.push(this.months);
                // this.Circle_Maker.push(this.years);
            },
        setRemain()
            {               
                




                this.years.multiple_num = 9;
                this.months.multiple_num= 1;
                this.date.multiple_num= 7;
                this.days.multiple_num= 1;
                this.hours.multiple_num= 3;


                this.years.grad = 81;
                this.months.grad= 12*2;
                this.date.grad= 31;
                this.days.grad= 7*3;
                this.hours.grad= 24;


                this.Circle_Maker.forEach(item => {
                    // console.log(item.remainMS);
                    item.remainMS = item.LastDate-this.today;
                    item.totalMS = item.LastDate-item.FirstDate;
                    // console.log(this.msToTime(item.totalMS));
                    item.remainH = this.msToTime(item.remainMS) +" / "+this.msToTime(item.totalMS);

                    item.Degree = ((item.totalMS-item.remainMS)/item.totalMS)*360;
                    console.log(item.Degree);

                    var obj = {
                        // 이번 : this.display,
                        // 남은시간: this.msToTime(item.remainMS),
                        // 전체시간: this.msToTime(item.totalMS),
                        a : this.display,
                        b: this.msToTime(item.remainMS),
                        c: this.msToTime(item.totalMS),
                    }
                    this.Display.push(obj);
                });
            },
        msToTime(duration) {
            // var milliseconds = parseInt((duration % 1000) / 100),
            var minutes = Math.floor((duration / (1000 * 60)) % 60);
            var hours = Math.floor((duration / (1000 * 60 * 60)));
            // var hours = Math.floor((duration / (1000 * 60 * 60)) % 24);
            // var days = Math.floor((duration / (1000 * 60 * 60* 24)) % 24);
            var result ="";
            // result +=(days>0)? (days+"일 ") : "";
            result += hours+":"+minutes;
            return result;
        }
  },
  components: { Mycircle , ProgressBar_Linear},
    
}
</script>
<style>
#Circle_Maker
{
    position: relative;
    /* margin: auto; */
    /* height: 100vh; */
    
}

p{
    color:white;
}
   

</style>