<template>
<div id = "ProgressBar_Linear">
  <div class="shell">
    <div class="bar" :style="{ width: progress + '%' }">
      <span>{{progress}}%</span>
    </div>
  <!-- <button @click="makeProgress">+</button> -->
</div>
</div>
</template>
<script>
export default {
    data(){
      return{
      // progress: 25
        progress: Math.round((this.Amount/this.Total)*100),
      }
  },
  props:
    {
        Amount : Number,
        Total : Number,
    },
  methods: {
    makeProgress() {
      if(this.progress < 100) {
        this.progress += 5;
      }
    }
  }
}
</script>
<style>
#ProgressBar_Linear {
  display: flex;
  justify-content: center;
  align-items: center;

  display: inline-block;
}

.shell {
  height: 10px;
  width: 100px;
  border: 1px solid #aaa;
  border-radius: 13px;
  /* padding: 3px; */
}

.bar {
  background: linear-gradient(to right, #d3be00d2, #ebe722d5);
  height: 100%;
  width: 15px;
  border-radius: 9px;
}
.bar span{
    float: right;
    /* padding: 0px; */
    margin: -5px;
    /* padding: 4px 5px; */
    color: #fff;
    font-size: 0.5rem
  }  

button {
  margin: 10px;
  border: 1px solid #aaa;
  background: none;
  border-radius: 50%;
  padding: 5px 8px;
  outline: none;
  cursor: pointer;
}
    
</style>